@import 'variable'

@mixin scrollbars($size, $foreground-color, $background-color: mix($foreground-color, white,  50%)) 
  // For Google Chrome
  &::-webkit-scrollbar 
    width:  $size
    height: $size
  &::-webkit-scrollbar-thumb 
    background: $foreground-color
  &::-webkit-scrollbar-track 
    background: $background-color
  // For Internet Explorer
  & 
    scrollbar-face-color: $foreground-color
    scrollbar-track-color: $background-color

body
    margin: 0
    font-family: sans-serif
    color: #ffffff
    display: flex
    max-height: 100vh
    width: 100%
    flex-direction: column
    line-height: 1.5
    overflow: hidden
    @media only screen and ( max-width: $max-width )
        height: auto
        overflow: auto
        overflow-x: hidden
    
p
    margin: 0
h1
    margin-top: 0
button
    border: 1px solid #ffffff
    padding: 10px 20px
    background-color: transparent
    color: #ffffff
    font-weight: bolder
    transition-duration: 300ms
    display: inline
    outline: none
    &:hover
        background-color: #ffffff
        color: $main-color

.fixpane
    width: 100%
    background-color: $pane-color
    font-size: 12px
    font-weight: bolder
    padding: 20px 0
    > *
        margin: 0 40px
        @media only screen and ( max-width: $max-width )
            margin: 20px
    &.top
        display: flex
        justify-content: space-between
        div + div
            text-align: right        
    &.bottom
        div
            text-align: right
    @media only screen and ( max-width: $max-width )
        padding: 1px

.main
    width: 100%
    height: calc( 100vh - 108px )
    display: flex
    align-items: stretch
    justify-content: stretch
    flex-direction: row
    > *
        display: flex
        align-items: center
        justify-content: center
        flex-direction: column
        padding: 40px
        text-align: center
        @media only screen and ( max-width: $max-width )
            padding: 20px
    .sidepane
        background-color: $side-color
        flex: 2
        @media only screen and ( max-width: $max-width )
            flex: auto
    .mainpane
        background-color: $main-color
        flex: 5
        overflow-y: scroll
        @include scrollbars(10px, $pane-color, $side-color)
        @media only screen and ( max-width: $max-width )
            overflow: hidden
            flex: auto
    @media only screen and ( max-width: $max-width )
        flex-direction: column-reverse
        height: auto
        min-height: 100vh